import * as three from 'three'
// import 'three'
import './controls/OrbitControls'
import './loaders/MTLLoader'
import './loaders/OBJLoader'
import deepAssign from 'deep-assign'
import TWEEN from '@tweenjs/tween.js'

import {
    bindMethods,
    getCenter,
    moveToCenter,
    moveToPosition,
    rotateObject,
    scaleObject,
    scaleToSize,
    parseVector3
} from './util'

// const THREE = window.THREE
const THREE = three
export default class ThreeModel{
    constructor(options){
        this.curProgress = 0

        bindMethods(this, ['render', 'onOrbitCtrlZoom', 'handleCanvasClick'])
        this.options = deepAssign({
            elm: 'ThreeJS',  // 
            debug: false,  // 
            scene: {  // 
                transparent: false,  // 
                background: 0x333333  // 
            },
            ctrl: {   // 
                type: 'orbit',  //
                options: {
                    autoRotate: true,  // 
                    zoomSpeed: 0.7, // 
                    maxPolarAngle: Math.PI,  // 
                    minPolarAngle: 0,
                    distance: 1.6,
                    maxDistance: 10,
                    minDistance: 0.1
                }
            },
            objects: {  // 
                camera: {  //  PerspectiveCamera
                    position: '0,1.6,1.6',
                    fov: 45,  //
                },
                models: [ // 3d

                ],
                lights: [ // 

                ],
            }
        }, options)

        this.cntModels = options.objects.models.length || 0

        // 3D1.scene 2.camera 3.renderer
        // 1.  document
        var elm = this.rootElm = document.getElementById(options.elm)
        var containerWidth = elm.clientWidth  , containerHeight = elm.clientHeight // 

        var scene = this.scene = new THREE.Scene()
        if (!this.options.scene.transparent){
            scene.background = new THREE.Color(this.options.scene.background)
        }

        // 
        // if(this.options.debug){
        //     var axisHelper = new THREE.AxesHelper( 10 )
        //     scene.add( axisHelper )
        // }

        // 2.
        var cameraOptions = this.options.objects.camera
        var camera = this.camera = new THREE.PerspectiveCamera(cameraOptions.fov || 45, containerWidth / containerHeight, 0.1, 1E4)
        camera.position.copy(parseVector3(cameraOptions.position))  // 
        camera.lookAt(scene.position)
        scene.add(camera)

        // 
        var lightsArr = this.options.objects.lights
        for(var light of lightsArr){
            this.addLight(light)
        }

        // 
        var models = this.options.objects.models
        for(var model of models) {
            this.addModel(model)
        }

        // 3.WebGL
        var renderer = this.renderer = new THREE.WebGLRenderer({
            antialias: !0,
            alpha: this.options.scene.transparent ? true : false
        })
        // console.log('renderer: ', renderer)
        renderer.shadowMap.enabled = true
		renderer.shadowMap.type = THREE.BasicShadowMap
		renderer.shadowMap.renderSingleSided = false
        renderer.setSize(containerWidth, containerHeight)
        renderer.setPixelRatio(window.devicePixelRatio)

        elm.appendChild(renderer.domElement)
        
        this.listeners = {}
        this.status = 'ready'

        // ThreeJsObject3D
        this.models = []
        // 
        this.raycaster = new THREE.Raycaster()
        this.renderer.domElement.addEventListener('click', this.handleCanvasClick)
        // 
        this.addController(this.options.ctrl)
    }

    addLight(light) {
        let options = light.options
        switch(light.type) {
            case 'hemisphere':
                // 
                var light1 = new THREE.HemisphereLight(options.skyColor, options.groundColor, options.intensity || 1.0)
                this.camera.add(light1)
                if(this.options.debug){
                    this.scene.add(new THREE.HemisphereLightHelper( light1, 2 ))
                }
                break
            case 'point':
                // 
                var pointLight = new THREE.PointLight( options.color || 0xffffff, options.intensity || 1.0, options.distance || 50)
        		pointLight.position.copy(parseVector3(options.position))
        		this.camera.add( pointLight )
                break
            case 'spot':
                // 
                break
            case 'direct':
                // 
                break
            case 'ambient':
                // 
                break
            default:
                break;
        }
    }

    addController(ctrl) {
        // 
        let options = ctrl.options
        switch(ctrl.type) {
            case 'orbit':
                let orbitCtl = this.orbitCtl = new THREE.OrbitControls(this.camera, this.rootElm)
                if(options.minDistance) orbitCtl.minDistance = options.minDistance
                if(options.maxDistance) orbitCtl.maxDistance = options.maxDistance
                orbitCtl.autoRotate = options.autoRotate || false
                orbitCtl.zoomSpeed = options.zoomSpeed || 1
                orbitCtl.maxPolarAngle = options.maxPolarAngle === 0? 0 : options.maxPolarAngle || Math.PI
                orbitCtl.minPolarAngle = options.minPolarAngle === 0? 0 : options.minPolarAngle || 0
                orbitCtl.addEventListener('zoom', this.onOrbitCtrlZoom)
                break
            default:
                break
        }
    }

    handleCanvasClick(event){
        let self = this
        if(self.models.length > 1){
            let mouse = new THREE.Vector2()
            mouse.x = ( event.offsetX / self.renderer.domElement.clientWidth ) * 2 - 1;
			mouse.y = - ( event.offsetY / self.renderer.domElement.clientHeight ) * 2 + 1;
            self.raycaster.setFromCamera(mouse, self.camera)
            // console.log('mouse clicked, x: ' + mouse.x + ", y: " + mouse.y)
            var intersects = self.raycaster.intersectObjects(self.models, true)
            if ( intersects.length > 0 ) {
                for(let intersect of intersects){
                    let obj = intersect.object
                    if(obj.canSelect || (obj.parent && obj.parent.canSelect)) {
                        // 3d
                        const target = obj.canSelect? obj : obj.parent
                        if(self.selected !== target) {
                            self.selected = target
                            self.centerModel(target)
                            self.emit('select', target)
                        }
                        break
                    }
                }
    		} else {
                // 
                if(self.selected) {
                    self.selected = false
                    self.emit('unselect')
                    self.centerAll()
                }
            }
        }
    }

    // 
    onOrbitCtrlZoom (e) {
        var zoom = e.zoom
        this.emit('zoom', zoom)
    }

    // 
    centerModel(mdl) {
        // 
        this.orbitCtl.autoRotate = false
        const center = getCenter(mdl)
        // console.log(`my center: (${center.x}, ${center.y}, ${center.z})`)
        // 
        new TWEEN.Tween(this.orbitCtl.target).to({
            x: center.x,
            y: center.y,
            z: center.z
        }, 1000).easing(TWEEN.Easing.Cubic.InOut).start()

        // 
        let position = parseVector3(this.options.objects.camera.position)
        new TWEEN.Tween(this.camera.position).to({
            x: center.x + position.x,
            y: center.y + position.y,
            z: center.z + position.z
        }, 1000).easing(TWEEN.Easing.Cubic.InOut).start()
    }

    // 
    centerAll(){
        this.orbitCtl.autoRotate = this.options.ctrl.autoRotate || false
        let radius = this.models.length * 0.16 // 
        let cameraDistance = 1.6 + 3.1 * radius // 
        let position = parseVector3(this.options.objects.camera.grpPosition) || {
            x: .0,
            y: cameraDistance,
            z: cameraDistance
        }
        new TWEEN.Tween(this.orbitCtl.target).to({
            x: 0,
            y: 0,
            z: 0
        }, 1000).easing(TWEEN.Easing.Cubic.InOut).start()
        new TWEEN.Tween(this.camera.position).to(position, 1000).easing(TWEEN.Easing.Cubic.InOut).start()
    }

    // 
    reArrangeModels() {
        if(this.models.length === 1){
            // 
            moveToCenter(this.models[0])
            // 
            // this.camera.position.set(.0, this.options.objects.camera.distance , this.options.objects.camera.distance )  // 
            // this.camera.position.copy(parseVector3(this.options.objects.camera.position))  // 
        } else {
            // 
            let delta = Math.PI * 2 / this.models.length // 
            let radius = this.models.length * 0.16 // 
            let cameraDistance = 1.6 + 3.1 * radius // 
            let idx = 0
            for(let mdl of this.models) {
                if(mdl.options.position) {
                    // 
                    moveToPosition(mdl, parseVector3(mdl.options.position))
                } else{
                    // 
                    moveToCenter(mdl)
                    // 
                    mdl.translateX(Math.cos(delta * idx) * radius)
                    mdl.translateZ(Math.sin(delta * idx) * radius)
                }
                if(mdl.options.scale) {
                    scaleObject(mdl, mdl.options.scale)
                }
                if(mdl.options.rotation){
                    rotateObject(mdl, mdl.options.rotation)
                }
                idx++
            }
            if(this.options.objects.camera.grpPosition) {
                this.camera.position.copy(parseVector3(this.options.objects.camera.grpPosition))  // 
            } else {
                this.camera.position.set(.0, cameraDistance, cameraDistance)  // 
            }
        }
    }

    // 
    addModel(model) {
        let self = this
        let options = deepAssign(
            {
                name: '_model_', // 
                size: 1.0, // 
                rotation: { // 
                    x: 0,
                    y: 0,
                    z: 0
                }
            }, model.options || {})
        let mtlLoader = new THREE.MTLLoader()
        mtlLoader.setCrossOrigin(true)
        mtlLoader.setPath(model.path)
        let that = this
        mtlLoader.load(model.mtl, function(materials){
            for(var mn in materials.materials){
                if(materials.materials.hasOwnProperty(mn)){
                    // 
                    materials.materials[mn].side = THREE.DoubleSide
                }
            }
            materials.preload()
            if (model.type === 'obj' || !model.type){
                var objLoader = new THREE.OBJLoader()
                objLoader.setMaterials(materials)
                objLoader.setPath(model.path)
                objLoader.load(model.file, function(object){
                    // 
                    object.name = model.name
                    object.metaModel = model
                    object.options = options
                    // 
                    object.canSelect = true
                    // 
                    scaleToSize(object, options.size || 1.0)
                    // 
                    moveToCenter(object)
                    self.models = []
                    self.models.push(object)
                    // 
                    if(self.options.debug){
                        self.scene.add(new THREE.BoxHelper(object, 0xffff00))
                    }
                    self.emit('loaded')
                    // console.log('objLoader. self.models.length : ', self.models )
                    // console.log('objLoader. self.cntModels : ', self.cntModels)
                    if(self.models.length === self.cntModels) {
                        //  
                        for(let m of self.models) {
                            // console.log('', m)
                            self.scene.add(m)
                        }
                        self.reArrangeModels()
                        self.emit('finished')
                    }
                    // console.log('objLoader.load: ', object)
                }, function( xhr ) {
                    if ( xhr && xhr.lengthComputable ) {
                        var percentComplete = xhr.loaded / xhr.total * 100
                        that.setProgress(Math.round( percentComplete, 2 ))
                        // console.log( Math.round( percentComplete, 2 ) + '% downloaded' )
                    } else {
                        that.setProgress(-1)
                    }
                }, function( xhr ) {
                    // console.error( xhr )
                })
            }
        })
    }

    // 
    setProgress(curPro){
        this.curProgress = curPro
    }

    // 
    getProgress(){
        // console.log('ThreeModel curPro', this.curProgress)
        return this.curProgress || 0
    }

    render(timestamp) {
        TWEEN.update()

        this.orbitCtl && this.orbitCtl.update()
        this.renderer && this.renderer.render(this.scene, this.camera)

        if(this.status === 'running' && this.render){
            requestAnimationFrame(this.render)
        } else {
            this.render = null
        }
    }

    start() {
        this.status = 'running'
        if(this.options.model && this.options.model.path){
            this.addModel(this.options.model)
        }
        this.render()
    }

    show3DModel(options) {
        if (this.status === 'ready') {
            this.start()
        } else if (this.status === 'running') {
            this.showAnother(options)
        }
    }

    showAnother(options){
        this.curProgress = 0
        if (this.scene && this.scene.children) {
            this.scene.children.map(mdl => {
                if(mdl.type === 'Mesh') {
                    mdl.material.dispose()
                    mdl.geometry.dispose()
                }
                this.scene.remove(mdl)
                return null
            })
        }
        this.scene = null

        var scene = this.scene = new THREE.Scene()
        if (!this.options.scene.transparent){
            scene.background = new THREE.Color(this.options.scene.background)
        }
        this.camera.lookAt(this.scene.position)
        this.scene.add(this.camera)

        this.options = deepAssign({
            elm: 'ThreeJS',  // 
            debug: false,  // 
            scene: {  // 
                transparent: false,  // 
                background: 0x333333  // 
            },
            ctrl: {   // 
                type: 'orbit',  //
                options: {
                    autoRotate: true,  // 
                    zoomSpeed: 0.7, // 
                    maxPolarAngle: Math.PI,  // 
                    minPolarAngle: 0,
                    distance: 1.6,
                    maxDistance: 10,
                    minDistance: 0.1
                }
            },
            objects: {  // 
                camera: {  //  PerspectiveCamera
                    position: '0,1.6,1.6',
                    fov: 45,  //
                },
                models: [ // 3d

                ],
                lights: [ // 

                ],
            }
        }, options)
        var models = this.options.objects.models
        for(var model of models) {
            this.addModel(model)
        }  
        this.render()
    }

    // 
    dispose() {
        this.status = 'stopped'
        this.renderer.domElement.removeEventListener('click', this.handleCanvasClick)
        this.orbitCtl.removeEventListener('zoom', this.onOrbitCtrlZoom)
        if (this.orbitCtl) {
            this.orbitCtl.dispose()
            this.orbitCtl = null
        }
        if( this.renderer && this.renderer.dispose) {
            this.renderer.dispose()
            this.renderer = null
        }
        if (this.scene && this.scene.children) {
            this.scene.children.map(mdl => {
                if(mdl.type === 'Mesh') {
                    mdl.material.dispose()
                    mdl.geometry.dispose()
                }
                this.scene.remove(mdl)
                return null
            })
        }
        this.scene = null
        this.obj3d = null
        this.listeners = null
    }

    emit(event, ...args) {
        var listeners = this.listeners[event] || []
        listeners.forEach(function (l) {
            l && l(...args)
        })
    }

    on(event, cb) {
        var listeners = this.listeners[event]
        if (!listeners) {
            listeners = this.listeners[event] = []
        }
        if (listeners.indexOf(cb) < 0) {
            listeners.push(cb)
        }
    }
}

