// 
// const THREE = window.THREE
import * as three from 'three'
const THREE = three

export const bindMethods = function(target, methods) {
    for(let mtd of methods){
        target[mtd] = target[mtd].bind(target)
    }
}

// 3d
export const getBoundingBox = function(object) {
    var box = new THREE.Box3()
    box.setFromObject(object)
    return box
}

// 3d
export const getSize = function(object) {
    var box = new THREE.Box3()
    box.setFromObject(object)
    return new THREE.Vector3(
        box.max.x - box.min.x,
        box.max.y - box.min.y,
        box.max.z - box.min.z
    )
}

// 3d
export const getCenter = function(object) {
    var box = new THREE.Box3()
    box.setFromObject(object)
    return new THREE.Vector3(
        (box.max.x + box.min.x) / 2.0,
        (box.max.y + box.min.y) / 2.0,
        (box.max.z + box.min.z) / 2.0
    )
}



// 
export const moveToCenter = function(object) {
    moveToPosition(object, new THREE.Vector3(0,0,0))
}

// 
export const moveToPosition = function(object, v){
    var box = new THREE.Box3()
    box.setFromObject(object)
    object.translateX(v.x - (box.max.x + box.min.x) / 2.0)
    object.translateY(v.y - (box.max.y + box.min.y) / 2.0)
    object.translateZ(v.z - (box.max.z + box.min.z) / 2.0)
}

// 
export const rotateObject = function(object, v) {
    object.rotation.set(v.x, v.y, v.z)
}


// 
export const scaleObject = function(object, scale) {
    // 
    const lastScale = object.lastScale || 1.0
    object.scale.set(lastScale * scale, lastScale * scale, lastScale * scale)
}

// 
export const scaleToSize = function(object, size) {
    var box = getBoundingBox(object)
    const sizeX = box.max.x - box.min.x, sizeY = box.max.y - box.min.y, sizeZ = box.max.z - box.min.z
    //1
    const diagonalSize = Math.sqrt(sizeX * sizeX + sizeY * sizeY + sizeZ * sizeZ)
    const scale = size / diagonalSize
    // console.log('object dimension, w: ' + sizeX + ', h: ' + sizeY + ', d: ' + sizeZ, ', scale: ' + scale)
    object.scale.set(scale, scale, scale)
    // 
    object.lastScale = scale
}


// 
export const parseVector3 = function(val) {
    if(!val)
        return null
    if(typeof val === 'string') {
        let arr = val.split(',')
        return new THREE.Vector3(parseFloat(arr[0]), parseFloat(arr[1]), parseFloat(arr[2]))
    } else if(Array.isArray(val)){
        return new THREE.Vector3(val[0], val[1], val[2])
    } else {
        return new THREE.Vector3(val.x, val.y, val.z)
    }
}



